import { Component, OnInit } from '@angular/core';
// import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { MainServicesService } from '../../main-services.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
// import { Ng4LoadingSpinnerService } from '../../../../node_modules/ng4-loading-spinner';
import { NgxSpinnerService } from "ngx-spinner";
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: any;

  remberMe: any = [];
  token: any;
  loginForm: FormGroup;

  responseData: any = {}
  constructor(public router: Router, public service: MainServicesService,
    public spinnerService: NgxSpinnerService
     ) { }
  ngOnInit() {
    this.loginForm = new FormGroup({
      "email": new FormControl('', ([Validators.required, Validators.pattern((/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,3})$/)), Validators.maxLength(50)])),
      "password": new FormControl('', ([Validators.required, Validators.maxLength(16), Validators.minLength(8)])),
      "remeberMe": new FormControl(false)
    })
  
    }


//***************Login Function**************/
login(data) {
  this.spinnerService.show();
let tempLogin = {
  "email": data.email,
  "password": data.password
}
// this.router.navigate(['/dashboard']);
this.service.postApi('admin/login', tempLogin, 0).subscribe(response => {
  console.log("in API >>>>>>>>>>",response["responseCode"])
if(response && response["responseCode"]==200)
{
  this.service.showSuccess( response["responseMessage"])

  this.router.navigate(['/dashboard']);

  localStorage.setItem('token',response["result"])

}
else{
  console.log("I am not res>>>>>>>>",response['responseMessage'])
  this.service.toastErr(response['responseMessage'])

}
this.spinnerService.hide();

},err=>{
  this.service.toastErr('Something went wrong.')
  this.spinnerService.hide();
})
}

//*************************prevent****** */
prevent(event){
var k = event.charCode;
if (k === 32) { return false }
}

}
